import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

@Component({
	selector: 'sq-basic-dashboard',
	templateUrl: './basic-dashboard.component.html',
	styleUrls: ['./basic-dashboard.component.scss']
})
export class BasicDashboardComponent implements OnInit {

	public route = 'facebook';

	constructor(
		private _router: Router,
	) {
		this._router.events.subscribe((event) => {
			if (event instanceof NavigationEnd) {
				//  console.log(event);
				const url = event.url;
				const urlParts = url.split('/');

				let section = urlParts[2];
				if (!section) {
					section = 'facebook';
				} else {
					this.route = section;
				}
			}
		});
	}

	ngOnInit() {
	}

}
