import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
/** GUARDS */
import { GuardsModule } from './../guards/guards.module';
import { BasicDashboardComponent } from './basic-dashboard.component';
import { FacebookComponent } from './facebook/facebook.component';
import { InstagramComponent } from './instagram/instagram.component';
/** COMPONENTS */

const routes: Routes = [
	{
		path: '', component: BasicDashboardComponent, children: [
			{ path: 'facebook', component: FacebookComponent },
			{ path: 'instagram', component: InstagramComponent },
			{ path: '**', redirectTo: 'facebook', pathMatch: 'full' }
		]
	}

];

/**
 * CONTENEDOR PRINCIPAL DE RUTAS EN COMPONENTS
 * @export
 * @class ComponentsRouting
 */
@NgModule({
	imports: [GuardsModule, RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class BasicDashboardRouting { }
