import { Component, OnInit } from '@angular/core';
import { PULSE_INSTAGRAM_COMUNITY_STATISTICS,
	PULSE_INSTAGRAM_ACTIVITY_STATISTICS,
	PULSE_INSTAGRAM_PERFORMANCE_STATISTICS } from 'src/app/data/pulse/mock-pulse-instagram';

@Component({
	selector: 'sq-instagram',
	templateUrl: './instagram.component.html',
	styleUrls: ['./instagram.component.scss']
})
export class InstagramComponent implements OnInit {

		// Style 1
		public graphXYStyle = {
			showHatchedArea: true,
			colorHatchedArea: '#9B9BD7',
			colorLine: '#9B9BD7',
			colorPoints: '#9B9BD7'
		};
		// Style selected
		public graphXYStyleSelected = {
			showHatchedArea: true,
			colorHatchedArea: '#3B3B3B',
			colorLine: '#3B3B3B ',
			colorPoints: '#3B3B3B',
		};
		public graphXYStyleSelectedWhite = {
			showHatchedArea: true,
			colorHatchedArea: '#FFFFFF',
			colorLine: '#FFFFFF',
			colorPoints: '#FFFFFF'
		};

		public timeFilter = 'monthly';
	public cardsCommunity = [];
	public cardsActivity = [];
	public cardsActivity2 = [];
	public cardsPerformance = [];
	public cardsPerformance2 = [];

	constructor() { }

	ngOnInit() {
		this.cardsCommunity = PULSE_INSTAGRAM_COMUNITY_STATISTICS(this.timeFilter);
		this.cardsActivity = PULSE_INSTAGRAM_ACTIVITY_STATISTICS(this.timeFilter).slice(0, 4);
		this.cardsPerformance = PULSE_INSTAGRAM_PERFORMANCE_STATISTICS(this.timeFilter).slice(0, 3);
		this.cardsPerformance2 = PULSE_INSTAGRAM_PERFORMANCE_STATISTICS(this.timeFilter).slice(3, 7);

	}

}
