export const PULSE_FACEBOOK_PERFORMANCE_INTERACTIONS_KEYS = ['reactions', 'shares', 'comments', 'clicks'];
export const PULSE_FACEBOOK_PERFORMANCE_INTERACTIONS_DESCRIPTIONS = ['Reactions', 'Shares', 'Comments', 'Clicks'];
export function generatePulseFacebookInteractions(timeFilterKey = 'annual') {
	const interactions = {
		keys: PULSE_FACEBOOK_PERFORMANCE_INTERACTIONS_KEYS,
		descriptionKeys: PULSE_FACEBOOK_PERFORMANCE_INTERACTIONS_DESCRIPTIONS,
		data: []
	};
	const data = generateDataArray(true, timeFilterKey);
	data.forEach(d => {
		const interaction = {};
		PULSE_FACEBOOK_PERFORMANCE_INTERACTIONS_KEYS.forEach(key => {
			interaction[key] = Math.floor(Math.random() *
				(100000000 - 0 + 1) + 0);
		});
		interaction['date'] = d.date;
		interactions.data.push(interaction);
	});

	return interactions;
}

