export const PULSE_FACEBOOK_RESPONSE_FEELING_KEYS = ['good', 'neutral', 'bad'];
export const PULSE_FACEBOOK_RESPONSE_FEELING_DESCRIPTIONS = ['Good', 'Neutral', 'Bad'];
export function generatePulseFacebookFeeling(timeFilterKey = 'annual') {
	const feeling = {
		keys: PULSE_FACEBOOK_RESPONSE_FEELING_KEYS,
		descriptionKeys: PULSE_FACEBOOK_RESPONSE_FEELING_DESCRIPTIONS,
		data: []
	};
	const data = generateDataArray(true, timeFilterKey);
	data.forEach(d => {
		const interaction = {};
		PULSE_FACEBOOK_RESPONSE_FEELING_KEYS.forEach(key => {
			interaction[key] = Math.floor(Math.random() *
				(100000000 - 0 + 1) + 0);
		});
		interaction['date'] = d.date;
		feeling.data.push(interaction);
	});

	return feeling;
}
