import * as moment from 'moment';

//#region 'Functions'
export function generateDataArray(highestPoint = true, timeFilterKey = 'annual') {
	let dataArray = [];
	if (timeFilterKey === 'annual') {
		dataArray = generateDataArrayMonth();
	}
	if (timeFilterKey === 'monthly') {
		dataArray = generateDataArrayDaysMonth();
	}
	if (timeFilterKey === 'weekly') {
		dataArray = generateDataArrayDays();
	}
	if (timeFilterKey === 'historical') {
		dataArray = generateDataArrayYears();
	}
	if (highestPoint) {
		getHighestPoint(dataArray);
	}

	return dataArray;
}
export function generateDataArrayMonth(nameRange = '2018', highestPoint = true) {
	const dataArray = [];
	const year = moment().year();
	for (let j = 0; j < 12; j++) {
		const value = Math.floor(Math.random() * (100000000 - 0 + 1) + 0);
		dataArray.push({
			date: `01/${j + 1}/${year}`,
			nameRange: nameRange,
			impressions: value,
			value: value,
			tooltipData: value
		});
	}
	return dataArray;
}
export function generateDataArrayDaysMonth(nameRange = '2018', highestPoint = true) {
	const dataArray = [];
	const daysInMonth = moment().daysInMonth();
	const month = moment().month() + 1;
	const year = moment().year();
	for (let j = 0; j < daysInMonth; j++) {
		const value = Math.floor(Math.random() * (100000000 - 0 + 1) + 0);
		dataArray.push({
			date: `${j + 1}/${month}/${year}`,
			nameRange: nameRange,
			impressions: value,
			value: value,
			tooltipData: value
		});
	}
	return dataArray;
}
export function generateDataArrayDays(nameRange = '2018', highestPoint = true) {
	const dataArray = [];
	const endDate = moment();
	const startDate = moment().subtract(1, 'week');
	// If you want an exclusive end date (half-open interval)
	for (const m = moment(startDate); m.isSameOrBefore(endDate); m.add(1, 'days')) {
		const value = Math.floor(Math.random() * (100000000 - 0 + 1) + 0);
		dataArray.push({
			date: m.format('DD/MM/YYYY'),
			nameRange: nameRange,
			impressions: value,
			value: value,
			tooltipData: value
		});
	}
	return dataArray;
}
