export function getHighestPoint(dataArray) {
	dataArray.forEach(d => {
		d.highestPoint = d.impressions === Math.max.apply(Math, dataArray.map((o) => o.impressions));
	});
}
export function getNameRange(timeFilterKey = 'annual') {
	let nameRange = '2018';
	if (timeFilterKey === 'annual') {
		nameRange = '2018';
	}
	if (timeFilterKey === 'monthly') {
		nameRange = 'Julio';
	}
	if (timeFilterKey === 'weekly') {
		nameRange = 'Julio';
	}
	if (timeFilterKey === 'historical') {
		nameRange = 'Histórico';
	}

	return nameRange;
}
// Genearate random data to development purpose
export function generateRandomDataFelling() {
	const data: any = [];
	for (let month = 1; month < 31; month++) {
		const malo = Math.round(Math.random() * (1420 - 300) + 300);
		const regular = Math.round(Math.random() * (920 - 100) + 300);
		const bueno = Math.round(Math.random() * (370 - 70) + 300);
		const m = { key: month, malo: malo, regular: regular, bueno: bueno };
		data.push(m);
	}
	return data;
}

