export function getRandomArbitrary(min, max) {
	return Math.random() * (max - min) + min;
}
export function genRandDecimal(min, max, decimalPlaces) {
	const rand = Math.random() * (max - min) + min;
	const power = Math.pow(10, decimalPlaces);
	return Math.floor(rand * power) / power;
}

export function zeroPad(num, length) {
	return num.toString().padStart(length, '0');
}
//#endregion 'Functions'

export function generateStatistics(timeFilterKey = 'annual') {
	const types = ['EQUALS', 'UP', 'DOWN'];
	const type = types[Math.round(getRandomArbitrary(0, types.length - 1))];
	const percentage = Math.round(getRandomArbitrary(0, 100));
	const symbol = getArithmeticComparative(type);
	let time = 'year';
	if (timeFilterKey === 'monthly') {
		time = 'month';
	}
	if (timeFilterKey === 'weekly') {
		time = 'week';
	}
	const subtitle = getSubtitleComparative(type, symbol, percentage, time);
	const statistics = {
		title: Math.floor(Math.random() * (250000000 - 0 + 1) + 0),
		type: type,
		subtitle: subtitle,
		percentage: percentage,
		symbol: '='
	};
	return statistics;
}

export function getArithmeticComparative(type) {
	switch (type) {
		case 'EQUALS': {
			return 'Igual';
			break;
		}
		case 'UP': {
			return '+';
			break;
		}
		case 'DOWN': {
			return '-';
			break;
		}
	}
}
export function getSubtitleComparative(type, symbol, percentage, time) {
	let subtitle = '';
	if (type === 'UP') {
		subtitle = `${symbol}${Math.abs(percentage).toFixed(2)}% vs last ${time}`;
	}
	if (type === 'DOWN') {
		subtitle = `${symbol}${Math.abs(percentage).toFixed(2)}% vs last ${time}`;
	}
	if (type === 'EQUALS') {
		subtitle = `Equals vs  last ${time}`;
	}
	return subtitle;
}

export function getPeriodText(timeFilterKey) {
	let text = 'year';
	switch (timeFilterKey) {
		case 'annual': {
			text = 'year';
			break;
		}
		case 'historical': {
			text = 'year';
			break;
		}
		case 'monthly': {
			text = 'month';
			break;
		}
		case 'weekly': {
			text = 'week';
			break;
		}
		default: {
			text = 'year';
			break;
		}
	}
	return text;
}

