/**
     * Get limit date {from,to}
     * @param timeFilterKey
     * @param compare
     * @return dateStartEnd: {start:'MM/DD/YYYY',end:''MM/DD/YYYY'}

     */
export function getPeriodLimitDates(timeFilterKey, compare = true, minYear = 2019) {
	const currentDate = new Date();
	const dayNumber = currentDate.getDate();
	const monthNumber = currentDate.getMonth();
	const yearNumber = currentDate.getFullYear();
	const dateStartEnd = { start: '', end: '' };
	const format = 'MM/DD/YYYY';
	switch (timeFilterKey) {
		case 'annual': {

			let monthCompare = 0;
			if (compare) {
				monthCompare = 12;
			}
			const substract = monthCompare + monthNumber;
			// console.log(yearNumber, monthNumber, dayNumber);
			// Range
			const to = moment([yearNumber, monthNumber, dayNumber]);
			// console.log(to.format(format).toString());
			const from = moment(to).subtract(substract, 'month').set('date', 1);
			dateStartEnd.start = from.format(format).toString();
			dateStartEnd.end = to.format(format).toString();
			// console.log(dateStartEnd);
			break;
		}
		case 'historical': {
			const substract = 1;
			// Range
			const to = moment([yearNumber, monthNumber, dayNumber]);
			const from = moment([yearNumber - substract, 0, 1]);
			dateStartEnd.start = from.format(format).toString();
			dateStartEnd.end = to.format(format).toString();
			break;
		}
		case 'monthly': {
			// Dates
			const pastDate = moment(currentDate).subtract(1, 'month');
			// Days
			let pastDateDays = 0;
			const currentDateDays = dayNumber;
			if (compare) {
				pastDateDays = pastDate.daysInMonth();
			}
			const substract = (currentDateDays - 1) + pastDateDays;
			// Range
			const to = moment([yearNumber, monthNumber, dayNumber]);
			const from = moment(to).subtract(substract, 'days');
			dateStartEnd.start = from.format(format).toString();
			dateStartEnd.end = to.format(format).toString();
			break;
		}
		case 'weekly': {
			// Dates
			const dayOfWeek = 7;
			let dayOfWeekPast = 0;
			if (compare) {
				dayOfWeekPast = 7;
			}
			const substract = dayOfWeek + dayOfWeekPast;
			// Range
			const to = moment([yearNumber, monthNumber, dayNumber]);
			const from = moment(to).subtract(substract, 'days');
			dateStartEnd.start = from.format(format).toString();
			dateStartEnd.end = to.format(format).toString();
			break;
		}
		default: {
			let monthCompare = 0;
			if (compare) {
				monthCompare = 12;
			}
			const substract = monthCompare + (monthNumber);
			// Range
			const to = moment([yearNumber, monthNumber, dayNumber]);
			const from = moment(to).subtract(substract, 'month').set('date', 1);
			dateStartEnd.start = from.format(format).toString();
			dateStartEnd.end = to.format(format).toString();
		}
	}
	let startDate = moment(dateStartEnd.start, format);
	const endDate = moment(dateStartEnd.end, format);
	if (startDate.toDate().getFullYear() < minYear) {
		startDate.set('year', minYear);
		const yearStart = startDate.toDate().getFullYear();
		const yearEnd = endDate.toDate().getFullYear();
		const monthStart = startDate.toDate().getMonth();
		const monthEnd = endDate.toDate().getMonth();
		if (yearStart === yearEnd) {
			if (monthStart > monthEnd) {
				startDate.set('month', monthEnd - 1);
			}
		}
		dateStartEnd.start = startDate.format(format).toString();
	}
	startDate = moment(dateStartEnd.start, format);
	const days = endDate.diff(startDate, 'days');
	return { dateStartEnd, days };
}

export function transformResponseSentiment(sentimentData) {

	const bestSentiment = {
		percentage: 0,
		value: 'bad'
	};
	const donut = {
		data: [
			{ key: 'good', value: 0, label: 'Good' },
			{ key: 'neutral', value: 0, label: 'Neutral' },
			{ key: 'bad', value: 0, label: 'Bad' }
		],
		icon: './assets/images/neutral-face.svg'
	};
	let sentimentValues = [
		{ key: 'good', percentage: 0 },
		{ key: 'neutral', percentage: 0 },
		{ key: 'bad', percentage: 0 }
	];
	if (sentimentData) {
		// Good
		let totalGood = 0;
		if (sentimentData.good) {
			totalGood = sentimentData.good.reduce((total, currentValue) => {
				return total + currentValue.value;
			}, 0);
		}
		// Bad
		let totalBad = 0;
		if (sentimentData.bad) {
			totalBad = sentimentData.bad.reduce((total, currentValue) => {
				return total + currentValue.value;
			}, 0);
		}
		// Neutral
		let totalNeutral = 0;
		if (sentimentData.neutral) {
			totalNeutral = sentimentData.neutral.reduce((total, currentValue) => {
				return total + currentValue.value;
			}, 0);
		}
		const totalSentiments = totalGood + totalNeutral + totalBad;
		if (totalSentiments > 0) {
			let percentageGood = 0;
			if (totalGood > 0) {
				percentageGood = Math.round((totalGood * 100) / totalSentiments);
			}
			let percentageNeutral = 0;
			if (totalNeutral > 0) {
				percentageNeutral = Math.round((totalNeutral * 100) / totalSentiments);
			}
			let percentageBad = 0;
			if (totalBad > 0) {
				percentageBad = Math.round((totalBad * 100) / totalSentiments);
			}
			sentimentValues = [
				{ key: 'good', percentage: percentageGood },
				{ key: 'neutral', percentage: percentageNeutral },
				{ key: 'bad', percentage: percentageBad }
			];
			const sentimentMax = sentimentValues.sort((a, b) => {
				return b['percentage'] - a['percentage'];
			})[0];
			if (sentimentMax && sentimentMax.percentage) {
				bestSentiment.percentage = sentimentMax.percentage;
				bestSentiment.value = sentimentMax.key;
			}
			// Set donut
			donut.data[0].value = percentageGood;
			donut.data[1].value = percentageNeutral;
			donut.data[2].value = percentageBad;
			// Set donut icon
			if (bestSentiment.value === 'good') {
				donut.icon = 'assets/images/feliz-bueno.svg';
			}
			if (bestSentiment.value === 'neutral') {
				donut.icon = 'assets/images/neutral-face.svg';
			}
			if (bestSentiment.value === 'bad') {
				donut.icon = 'assets/images/triste-malo.svg';
			}
		}

	}
	return { bestSentiment, donut, sentimentValues };

}
