//#region "functions"
export function generateMockPosts() {
	const postStatus = [{ key: 'good' }, { key: 'bad' }];
	const posts = [];
	const contents = POSTS_CONTENTS;
	let i = 0;
	postStatus.forEach(s => {
		let startdate = moment();
		// for (let i = 0; i < 5; i++) {
		const types = POSTS_TYPES;
		const max = POSTS_TYPES.length - 1;
		const min = 0;
		const t = types[Math.round(getRandomArbitrary(min, max))];
		const contentIndex = Math.floor(Math.random() * contents.length);

		// const t = types[i];
		const post = {
			type: t,
			location: 'East Siberia ',
			timestamp: _.cloneDeep(startdate),
			brand: {
				name: 'adidas',
				user: 'adidas',
				avatar: 'assets/images/brands/avatars/adidas.png'
			},
			// HTML
			content: contents[contentIndex],
			media: {
				value: `assets/images/posts/post${i + 1}.png`,
				kind: 'image'
			},
			performance: [
				{ type: 'likes', value: 780 },
				{ type: 'comments', value: 780 },
				{ type: 'reproductions', value: 780 }
			],
			status: s
		};
		posts.push(post);
		startdate = startdate.subtract(1, 'hours');
		i++;

		// }
	});
	return posts;
}

export function generateInstagramPostTable() {
	const postTable = [];

	for (let i = 0; i < 5; i++) {
		const type = POSTS_TYPES[Math.round(getRandomArbitrary(0, POSTS_TYPES.length - 1))];
		const postItem = {
			post: {
				cover: `assets/images/posts/post${i + 1}.png`,
				description: `A bright and bold take on the #EQT Support ADV, transforms the aggressive
                modern EQT design into a perfect shoe for …trendsetters of all ages. Available October 5th.`,
				time: new Date(),
				type: type
			},
			interactions: 27,
			comments: 1829,
			likes: 1829,
			impressions: 234,
			postReach: 826,
			engagementRate: 0.5,
			shared: 2343,
			views: 467,
			saves: 45678,
			affinity: 60
		};
		postTable.push(postItem);
	}
	return postTable;
}

