import { Component, OnInit } from '@angular/core';
import { PULSE_FACEBOOK_COMUNITY_STATISTICS,
	PULSE_FACEBOOK_ACTIVITY_STATISTICS,
	PULSE_FACEBOOK_PERFORMANCE_STATISTICS, 
	PULSE_FACEBOOK_CONTENT_POSTS_TABLE} from 'src/app/data/pulse/mock-pulse-facebook';
import { BENCH_FACEBEOOK_GENERAL_VERTICAL } from 'src/app/data/bench/mock-bench-facebook';

@Component({
	selector: 'sq-facebook',
	templateUrl: './facebook.component.html',
	styleUrls: ['./facebook.component.scss']
})
export class FacebookComponent implements OnInit {

	// Style 1
	public graphXYStyle = {
		showHatchedArea: true,
		colorHatchedArea: '#9B9BD7',
		colorLine: '#9B9BD7',
		colorPoints: '#9B9BD7'
	};
	// Style selected
	public graphXYStyleSelected = {
		showHatchedArea: true,
		colorHatchedArea: '#3B3B3B',
		colorLine: '#3B3B3B ',
		colorPoints: '#3B3B3B',
	};

	public graphXYStyleSelectedWhite = {
		showHatchedArea: true,
		colorHatchedArea: '#FFFFFF',
		colorLine: '#FFFFFF',
		colorPoints: '#FFFFFF'
	};

	public timeFilter = 'monthly';
	public cardsCommunity = [];
	public cardsActivity = [];
	public cardsActivity2 = [];
	public cardsPerformance = [];
	public cardsPerformance2 = [];
	public postTable = [];
	public dataBenchFans;

	constructor() { }

	ngOnInit() {
		this.cardsCommunity = PULSE_FACEBOOK_COMUNITY_STATISTICS(this.timeFilter);
		this.cardsActivity = PULSE_FACEBOOK_ACTIVITY_STATISTICS(this.timeFilter).slice(0, 4);
		this.cardsActivity2 = PULSE_FACEBOOK_ACTIVITY_STATISTICS(this.timeFilter).slice(4, 8);
		this.cardsPerformance = PULSE_FACEBOOK_PERFORMANCE_STATISTICS(this.timeFilter).slice(0, 4);
		this.cardsPerformance2 = PULSE_FACEBOOK_PERFORMANCE_STATISTICS(this.timeFilter).slice(4, 8);
		this.postTable = PULSE_FACEBOOK_CONTENT_POSTS_TABLE.posts;
		this.dataBenchFans = BENCH_FACEBEOOK_GENERAL_VERTICAL('weekly')[0]

	}

}
