export function geenrateMockPosts() {
	const postStatus = [POSTS_STATUS[0]];
	const posts = [];
	const contents = POSTS_CONTENTS_BENCH;
	const brands = BENCH_BRANDS;
	postStatus.forEach(s => {
		let startdate = moment();
		brands.forEach((b, index) => {
			const types = POSTS_TYPES;
			const t = types[index];
			const contentIndex = Math.floor(Math.random() * contents.length);
			const post = {
				type: t,
				timestamp: _.cloneDeep(startdate),
				brand: b,
				// HTML
				content: contents[contentIndex],
				media: {
					value: `assets/images/posts/post${index + 1}.png`,
					kind: 'image'
				},
				reactions: [
					{ type: 'love', value: 50000 },
					{ type: 'haha', value: 40000 },
					{ type: 'wow', value: 30000 },
					{ type: 'sad', value: 20000 },
					{ type: 'angry', value: 10000 },
					{ type: 'like', value: 60000 }
				],
				performance: [
					{ type: 'comments', value: 27 },
					{ type: 'shared', value: 70 }
				],
				status: s
			};
			posts.push(post);
			startdate = startdate.subtract(1, 'hours');
		});
	});
	return posts;
}

